.pricing{
    width: 100%;
    margin-top: 5rem;
}
.container{
    max-width: 1240px;
    padding: 1rem;
    margin: auto;
    overflow: hidden;
}
.top-content{
    display: grid;
    grid-template-columns: 3fr 1fr;
    border-bottom: 1px solid var(--lightgrey);
    padding-bottom: 3rem;
}
.top-content h2{
    font-size: 38px;
    padding-bottom: .5rem;
}
.top-content span{
    color:var(--primary)
}
.top-content p{
    color:var(--grey);
}
.bottom-content{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap:10px;
    margin: 3rem auto;
}
.card{
    display: flex;
    flex-direction: column;
    padding: 1rem 1rem 1rem 0;
}
.card div{
    display: flex;
    border-bottom: 1px solid var(--lightgrey);
    padding: 1rem 0;
}
.card .no-border{
    border:none;
}
.card span{
    color:var(--primary);
    text-transform: uppercase;
    font-weight: 500;
    font-size: 14px;
}
.card h3{
    font-size: 28px;
    margin: 0.7rem 0;
}
/* responsive */
@media screen and (max-width:960px){
    .top-content{
        grid-template-columns: 1fr;
    }
    .container .bottom-content{
        display: none;
    }
    .top-content h2{
        font-size: 32px;
    }
    .btn-div{
        padding-top: 5px;
    }
}