.testimonial{
    width: 100%;
    color:#fff;
    margin: 3rem 0;
}
.testimonial .container{
    max-width: 1240px;
    padding: 1rem;
    margin: auto;
    overflow: hidden;
}
.outline{
    background-image:linear-gradient(to left, rgba(73, 50, 173, .5), rgba(73, 50, 173, .9)),
                                    url('../assets/people.jpg');
    width: 100%;
    height: 450px;
    background-position: left;
    border-radius: 10px;
    background-size: cover;
}
.testimonial .content{
    display: flex;
    flex-direction: column;
    height: 100%;
    max-width: 50%;
    justify-content: center;
    padding: 3rem;
   
}
.body{
    padding: 1.2rem 0;
    color:#fff;
}
.name p{
    font-size: 14px;
    color:#fff;
}
/* responsive */
@media screen and (max-width:960px){
    .testimonial{
        margin: 1rem 0;
    }
    .testimonial .content{
        max-width: 100%;
    }
}