@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&family=Poppins:wght@400;600&display=swap');
*
{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Inter',sans-serif;
}
:root{
    --primary:#6948ff;
    --secondary:#553bcb;
    --darkgrey:#22262a;
    --lightgrey:#d3d3d3;
    --grey: #808080;
}
body{
    color:#282828;
}
ul{
    list-style: none;
}
a{
    text-decoration: none;
    color:#fff;
}
.primary-color{
    color:var(--primary);
}
.button{
    width: 100%;
    padding: 8px 0;
    border-radius: 4px;
    border:var(--primary);
    background-color: var(--primary);
    color:#fff;
    cursor: pointer;
    outline: none;
}
.button:hover{
    border:var(--secondary);
    background-color: var(--secondary);
}